import React, { useCallback, useMemo, useState } from 'react';
import { Member, Social } from 'src/models';
import { Typography, makeStyles, Theme } from '@material-ui/core';
import { SocialIcons } from 'src/constants';
import MemberProfileImage from '../Utils/MemberProfileImage';

const useStyles = makeStyles<Theme>(({ palette, spacing }) => ({
  root: {
    position: 'relative',
  },
  info: {
    marginTop: spacing(2),
    textAlign: 'center',
  },
  title: {
    fontWeight: 'bold',
  },
  links: {
    margin: `${spacing(2)}px 0 0 0`,
    padding: 0,
    display: 'flex',
    justifyContent: 'center',
  },
  linkIcon: {
    listStyle: 'none',
    margin: `0 ${spacing(1)}px`,
    '& i': {
      color: palette.text.secondary,
      transition: 'transform 0.2s ease-in-out, opacity 0.2s ease-in-out',
      '&:hover': {
        transform: 'translate(0, -5px)',
        opacity: 0.8,
      },
    },
  },
}));

interface Props {
  member: Member;
  backgroundColor?: string;
}
const MemberCard = ({ member, backgroundColor = 'transparent' }: Props) => {
  const profileImage = member.profileImg;
  const [viewBoxSize, setViewboxSize] = useState(0);
  const ref = useCallback((el: HTMLElement) => {
    if (el != null) {
      setViewboxSize(el.clientWidth);
    }
  }, []);

  const socials: Array<[string, string | undefined]> = useMemo(
    () => Object.entries(member.social).filter(([, socialUrl]) => Boolean(socialUrl)),
    [member.social]
  );

  const styles = useStyles();

  return (
    <div className={styles.root}>
      <div ref={ref}>
        <MemberProfileImage
          size={viewBoxSize}
          imageSrc={profileImage.childImageSharp?.fluid?.src ?? ''}
          backgroundColor={backgroundColor}
        />
      </div>
      <div className={styles.info}>
        <Typography display="block" component="h3" variant="subtitle1" className={styles.title}>
          {member.name.ko} / {member.name.en}
        </Typography>
        {member.company && <Typography variant="caption">{member.company}</Typography>}
        <Typography display="block" variant="caption">
          {member.role}
        </Typography>
      </div>
      <div>
        <ul className={styles.links}>
          {socials.length > 0 ? (
            socials.map(([key, socialUrl]) => (
              <li key={key} className={styles.linkIcon}>
                <a href={socialUrl} target="_blank" rel="noopener noreferrer">
                  <i className={`${SocialIcons[key as keyof Social]}`} />
                </a>
              </li>
            ))
          ) : (
            <li className={styles.linkIcon} />
          )}
        </ul>
      </div>
    </div>
  );
};

export default MemberCard;
