import React, { useEffect } from 'react';
import Page from 'components/Layout/Page';
import MainJumbotron from 'components/Main/MainJumbotron';
import MemberSection from 'components/Main/MemberSection';
import ActivitySection from 'components/Main/ActivitySection';
import ContactSection from 'src/components/Main/ContactSection';
import IntroSection from 'src/components/Main/IntroSection';
import { logger } from '@lubycon/logger';

const mainPageLogger = logger.getPageLogger('main_page');

const Index = () => {
  useEffect(() => {
    mainPageLogger.view();
  }, []);

  return (
    <Page>
      <MainJumbotron />
      <IntroSection />
      <ActivitySection />
      <MemberSection />
      <ContactSection />
    </Page>
  );
};

export default Index;
